.icon-play {
  background-image: url(./hamburger.png);
  background-size: cover;
  display: inline-block;
  margin-top: 8px;
  margin-left: 0px;
  margin-right: 10px;
  height: 20px;
  width: 20px;
}

.dropdown-toggle::after {
  display: none;
}

.divider:after,
.divider:before {
  content: "";
  flex: 1;
  height: 1px;
  background: black;
}

.h-custom {
  height: calc(100% - 73px);
}

/* NEW AUTOCOMPLERE USING TYPEHEAD */

body {
  margin: 30px;
}

.form-control {
  padding-right: 34px;
}

.toggle-button {
  background: transparent;
  border: 0;
  bottom: 0;
  padding: 0 12px;
  position: absolute;
  right: 0;
  top: 0;
}

.div-table {
  /* border: 1px solid red; */
  text-align: center;
}

table {
  /* border: 1px solid blue; */
  display: inline-table;
  /* outline-color: white; */
  /* outline-style: solid; */
  background: white;

}

td {
  text-align: left;
}

span {
  color: white;
  /* color: rgb(245, 118, 59); */
}

.logo-picture {
  width: 20%;
  height: 20%;
}

.city-state {
  font-size: 1.3em;
  font-style: italic;
  font-weight: bolder;
  color: white;
}

.rating-avg {
  /* color: blue; */
  font-size:90%;
  font-weight: bold;
  background: rgba(68, 67, 67, 0.966);
  /* color: rgb(245, 118, 59); */
  color: white;
}

.rating-avg2 {
  /* color: blue; */
