p {
  margin-bottom: 0;
}

.rate-comment {
  color: black;
  font-size: 90%;
}

.line-delete .user {
  float:left;
  font-size: 100%;
  font-style: normal;
  font-weight: normal;
}

.line-delete .action {
  float:right;
}

.usr-name {
  color: darkblue;
  font-weight: bold;
}

.time-stamp {
  color: darkblue;
  font-size: 80% !important;
}

.delete-button {
  font-size: 80%;
  padding-bottom: 2px;
  border-radius: 5px;
  background: rgba(112, 112, 112, 0.836);
  color: white;
  font-weight: bold;
}

.button-right {
  background: rgba(112, 112, 112, 0.836);
  color: white;
  font-weight: bold;
}

.link-line {
  color: rgb(5, 14, 94);
}

.date-time {
  /* color: blue; */
  font-size: 78%;
  
}

.city-separator {
  background-color: rgba(68, 67, 67, 0.966);
  color: white;
}

.box {
  border:1px solid white;
  color:rgb(36, 36, 49);
}

.box:nth-last-child(even) {
  background: rgb(230, 235, 232);
}

.box:nth-last-child(odd) {
  background: rgb(255, 231, 204);
}

.d-table {
  display: table;
}

.d-table-cell {
  display: table-cell;
}

.w-100 {
  width: 100%;
}

.tar {
  text-align: right;
}















.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}